:root {
  --gridPadding: 20px;
}

body {
  background-color: rgb(20, 20, 20);
}

.header {
  color: white;
  text-align: center;
}

.header a {
  color: white;
}

.layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* center items along the horizontal axis */
  align-items: center; /* center items along the vertical axis */
  gap: 40px;
}

.revealSliderItem {
  position: relative;
  width: 80vw;
  height: 80vh;
  display: flex;
  overflow: hidden;
  border-radius: 20px;
  /* background-color: rgb(112, 31, 31); */
}

.revealSliderItem img {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 20px;
  /* background-color: rgb(29, 168, 36); */
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100vh; */
  width: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
  grid-gap: 20px;
  width: 100%;
  height: calc(100% - (2 * var(--gridPadding)));
  padding: var(--gridPadding);
  background-color: rgb(20, 20, 20);
}
